import throttle from "lodash.throttle";
import seed from "seedrandom";
import simplex from "./noise.js";
const rng = seed(window.location.pathname || "madebymike");
const offset = rng() * 10000;
const canvasScaleFactor = 1;
console.log(offset);
const noise = new simplex({
  random: rng,
  amplitude: 100,
  frequency: 0.00005,
  max: 255,
  min: 0
});
window.noise = noise;
/////////////
function HSLToRGB(h, s, l) {
  // Must be fractions of 1
  s /= 100;
  l /= 100;

  let c = (1 - Math.abs(2 * l - 1)) * s,
    x = c * (1 - Math.abs(((h / 60) % 2) - 1)),
    m = l - c / 2,
    r = 0,
    g = 0,
    b = 0;

  if (0 <= h && h < 60) {
    r = c;
    g = x;
    b = 0;
  } else if (60 <= h && h < 120) {
    r = x;
    g = c;
    b = 0;
  } else if (120 <= h && h < 180) {
    r = 0;
    g = c;
    b = x;
  } else if (180 <= h && h < 240) {
    r = 0;
    g = x;
    b = c;
  } else if (240 <= h && h < 300) {
    r = x;
    g = 0;
    b = c;
  } else if (300 <= h && h < 360) {
    r = c;
    g = 0;
    b = x;
  }
  r = Math.round((r + m) * 255);
  g = Math.round((g + m) * 255);
  b = Math.round((b + m) * 255);

  return [r, g, b];
}

function generateDemoCanvas() {
  const canvas = document.createElement("canvas");
  document.body.appendChild(canvas);
  canvas.width = window.innerWidth / canvasScaleFactor;
  canvas.height =
    Math.max(
      document.body.scrollHeight,
      document.body.offsetHeight,
      document.documentElement.clientHeight,
      document.documentElement.scrollHeight,
      document.documentElement.offsetHeight
    ) / canvasScaleFactor;
  var ctx = canvas.getContext("2d");

  var imgData = ctx.createImageData(canvas.width, canvas.height);
  var data = imgData.data;
  console.log(
    noise.scaled([
      canvas.width * canvasScaleFactor,
      canvas.height * canvasScaleFactor
    ]),
    "bottom-right"
  );

  for (var i = 0; i < data.length; i += 4) {
    var x = (i / 4) % canvas.width;
    var y = (i / 4 - x) / canvas.width;
    const val = noise.scaled([
      x * canvasScaleFactor + offset,
      y * canvasScaleFactor + offset
    ]);
    const rgb = HSLToRGB(val, 80, 90);
    data[i] = rgb[0];
    data[i + 1] = rgb[1];
    data[i + 2] = rgb[2];
    data[i + 3] = 255;
  }

  ctx.putImageData(imgData, 0, 0);

  const style = document.createElement("style");
  style.innerHTML = `canvas {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    border: solid 1px;
    z-index: -1;}`;
  document.body.appendChild(style);
}

const doc = document.documentElement;
const throttledColor = throttle(e => {
  const val = Math.round(noise.scaled([e.pageX + offset, e.pageY + offset]));
  console.log([e.pageX, e.pageY], val);
  doc.style.setProperty("--HUE", val);
});

doc.addEventListener("scroll", throttledColor, false);
doc.addEventListener("mousemove", throttledColor, false);
// generateDemoCanvas()
